<template>
  <div class="col-md-3 mt-4">
    <ul class="list-group sticky-top">
      <a class="list-group-item list-group-item-light" href="#" 
        @click.prevent="toCategoryPage('', 1)" :class="{'active': isActive == 1}"></a>
      <a class="list-group-item list-group-item-light" href="#" 
        @click.prevent="toCategoryPage('', 2)" :class="{'active': isActive == 2}"></a>
      <a class="list-group-item list-group-item-light" href="#" 
        @click.prevent="toCategoryPage('', 3)" :class="{'active': isActive == 3}"></a>
      <a class="list-group-item list-group-item-light" href="#" 
        @click.prevent="toCategoryPage('', 4)" :class="{'active': isActive == 4}"></a>
    </ul>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isActive: 1,
      }
    },
    methods: {
      toCategoryPage(category, n) {
        this.isActive = n;
        this.$bus.$emit('category-search', category);
      }
    }
  }
</script>




